/* inter-300 - latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Inter";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/inter-v13-latin-300.woff2") format("woff2"); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
/* inter-500 - latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/inter-v13-latin-500.woff2") format("woff2"); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
/* marcellus-regular - latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Marcellus";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/marcellus-v13-latin-regular.woff2") format("woff2"); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}

html {
  font-size: 18px;
}

body {
  font-family: var(--ff-body);
  font-weight: var(--fw-300);
  font-size: var(--fs-400);
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
  font-family: var(--ff-headings);
  text-wrap: balance;
  color: var(--col-blue-900);
}

h1,
.h1 {
  font-size: var(--fs-800);
  line-height: var(--lh-900);
  font-style: normal;
  font-weight: var(--fw-700);
}

h2,
.h2 {
  font-size: var(--fs-700);
  font-weight: var(--fw-400);
  font-style: normal;
  line-height: var(--lh-800);
}
h3,
.h3 {
  font-size: var(--fs-600);
  font-weight: var(--fw-400);
  line-height: var(--lh-700);
}
h4,
.h4 {
  font-size: var(--fs-500);
  font-weight: var(--fw-500);
  line-height: var(--lh-700);
}

.fw-500,
strong {
  font-weight: var(--fw-500);
}

.fw-500 {
  font-weight: var(--fw-500);
}

.w-short {
  max-width: 65ch;
}

.w-short-45 {
  max-width: 45ch;
}

.has-x-large-font-size,
.fs-800 {
  font-size: var(--fs-800);
}
.has-large-font-size,
.fs-700 {
  font-size: var(--fs-700);
}

.fs-400 {
  font-size: var(--fs-400);
}

.has-medium-font-size,
.fs-500 {
  font-size: var(--fs-500);
}

.has-small-font-size,
.fs-300 {
  font-size: var(--fs-300);
}

.has-text-align-center {
  text-align: center;
}

.text-balance {
  text-wrap: balance;
}
.text-pretty {
  text-wrap: balance;
  text-wrap: pretty;
}

.ff-heading {
  font-family: var(--ff-headings);
}
.ff-body {
  font-family: var(--ff-body);
}
